<template>
  <view>
    <view class="navbar">
      <text
        v-for="(item, index) in navbar"
        :class="currentTab == index ? 'active' : ''"
        class="item"
        :key="index"
        @click="navbarTap(index)"
        >{{ item }}</text
      >
    </view>

    <view class="page-header" v-for="(item, index) in dataList" :key="index">
      <view class="header-title">
        <view>
          <image :src="iconMaintenance" mode=""></image>
          <text v-if="item.address" class="header-titele-text">{{
            item.address
          }}</text>
          <text v-else class="header-body-right">-</text>
        </view>
      </view>
      <view class="header-body">
        <text class="header-body-left">申请时间：</text>
        <text v-if="item.createTime" class="header-body-right showAll">{{
          item.createTime
        }}</text>
        <text v-else class="header-body-right">-</text>
      </view>
      <view v-if="currentTab == 2" class="maintenance-tag">
        <image
          src="../../assets/imgs/icon/icon-maintenance-success.png"
          mode="widthFix"
        ></image>
      </view>
      <view class="header-body">
        <text class="header-body-left">当前状态：</text>
        <text v-if="item.statusName" class="header-body-right">{{
          item.statusName
        }}</text>
        <text v-else class="header-body-right">-</text>
      </view>
      <view class="login-btn" v-if="currentTab !== 2">
        <view class="maintenance-loading" v-if="currentTab == 0">
          <navigator
            class="maintenance-loading-transfer"
            @click="openDetail(item)"
            aria-role="button"
            url="javascript:"
          >
            查 看
          </navigator>
        </view>
        <view class="maintenance-loading" v-if="currentTab == 1">
          <navigator
            class="maintenance-loading-transfer"
            @click.stop="handleTransfer(item)"
            aria-role="butt on"
            url="javascript:"
          >
            评 价
          </navigator>
        </view>
      </view>
    </view>
    <view class="weui-loadmore weui-loadmore_line" v-if="loaded">
      <view class="weui-loadmore__tips weui-loadmore__tips_in-line"
        >已全部加载</view
      >
    </view>

    <view :hidden="userFeedbackHidden" class="popup_content">
      <view class="popup_title">用户评价</view>
      <uni-section>
        <view class="uni-steps" style="margin-top: -20rpx">
          <view class="uni-steps-name">整体评价：</view>
          <view class="uni-steps-rate">
            <uni-rate size="35" :value="tempInfo.overall" />
          </view>
        </view>
        <view class="uni-steps">
          <view class="uni-steps-name">交警：</view>
          <view class="uni-steps-rate">
            <uni-rate size="35" :value="tempInfo.trafficPolice" />
          </view>
        </view>
        <view class="uni-steps">
          <view class="uni-steps-name">120：</view>
          <view class="uni-steps-rate">
            <uni-rate size="35" :value="tempInfo.medical" />
          </view>
        </view>
        <view class="uni-steps">
          <view class="uni-steps-name">119：</view>
          <view class="uni-steps-rate">
            <uni-rate size="35" :value="tempInfo.fire" />
          </view>
        </view>
        <view class="uni-steps" style="padding-bottom: 60rpx">
          <view class="uni-steps-name">评价描述：</view>
          <view class="uni-steps-rate" style="margin-top: 50rpx">
            <uni-easyinput
              type="textarea"
              v-model="tempInfo.remark"
              placeholder="请输入"
            />
          </view>
        </view>
      </uni-section>
      <view @click="submitFeedback()" class="buttons">
        <text class="popup_button">确定</text>
      </view>
    </view>
    <view
      class="popup_overlay"
      :hidden="userFeedbackHidden"
      @click="hideDiv()"
    ></view>

    <uni-load-more :status="status" />
  </view>
</template>
<script>
export default {
  data() {
    return {
      status: "more",
      dialogShow: false,
      userFeedbackHidden: true,
      loading: false,
      loaded: false,
      name: null,
      loginUserId: null,
      transferIndex: null,
      transferId: null,
      totalCountNo: null,
      page: 1,
      msgType: null,
      _USER_ID: null,
      iconMaintenance:
        "data:image/png;base64,",
      dataList: [],
      buttons: [
        {
          text: "取消",
        },
        {
          text: "确定",
        },
      ],
      oneButton: [
        {
          text: "确定",
        },
      ],
      array: [],
      nameList: [
        {
          name: "123",
        },
      ],
      navbar: ["处理中", "已结束", "已评价"],
      currentTab: 0,
      listQuery: {
        pageSize: 4,
        pageNumber: 1,
        sortColumn: "createTime",
        sortDirection: "desc",
      },
      tempInfo: {
        rescueInfoId: "",
        overall: 5,
        medical: 5,
        fire: 5,
        trafficPolice: 5,
        remark: "",
      },
    };
  },
  onLoad() {
    this.getList(true);
  },
  //触底函数
  onReachBottom() {
    let that = this;
    this.status = "loading";
    if (!that.loading) {
      that.loading = true;
      that.loaded = false;
    }
    console.log(that.dataList.length);
    console.log(that.totalCountNo);
    if (that.dataList && that.dataList.length < that.totalCountNo) {
      // let newPageNo = that.data.page++
      // console.log(that.data.page++)

      let newPageNo = (that.page += 1);
      console.log(newPageNo);
      this.listQuery.pageNumber = newPageNo;
      this.getList();
    } else {
      this.status = "noMore";
    }
  },
  methods: {
    openDetail(e) {
      console.log(e);
      let id = e.id;
      wx.navigateTo({
        url: "/pages/historyDetail/index?dataId=" + id,
      });
    },
    init() {
      this.tempInfo = {
        rescueInfoId: "",
        overall: 5,
        medical: 5,
        fire: 5,
        trafficPolice: 5,
        remark: "",
      };
    },
    // 切换tab
    navbarTap: function (e) {
      console.log(e);
      this.listQuery.pageNumber = 1;
      this.page = 1;
      this.currentTab = e;
      this.dataList = [];
      if (e == 0) {
        this.listQuery.statusList = [
          "1",
          "2",
          "3",
          "4",
          "6",
          "7",
          "9",
          "10",
          "12",
          "13",
        ];
      } else if (e == 1) {
        this.listQuery.statusList = ["17"];
      } else if (e == 2) {
        this.listQuery.statusList = ["18"];
      }
      setTimeout((e) => {
        this.getList();
      }, 100);
    },
    handleTransfer: function (e) {
      this.init();
      this.tempInfo.rescueInfoId = e.id;
      this.userFeedbackHidden = false;
    },
    hideDiv: function (e) {
      this.userFeedbackHidden = true;
    },

    submitFeedback: function (e) {
      this.tempInfo.status = "18";
      this.$http.evaluationInfo(this.tempInfo).then((res) => {
        const { status, data } = res;
        if (status == 200) {
          uni.showToast({
            title: "评价成功",
          });
          this.hideDiv();
        }
      });
    },
    // 列表接口
    getList: function (e) {
      if (e) {
        this.listQuery.statusList = [
          "1",
          "2",
          "3",
          "4",
          "6",
          "7",
          "9",
          "10",
          "12",
          "13",
        ];
      }
      this.$http.historyList(this.listQuery).then((res) => {
        const { status, data } = res;
        if (status == 200) {
          let list;
          let dataArray = data.records;
          this.totalCountNo = data.total;
          console.log(this.totalCountNo);
          console.log(dataArray.length);
          if (this.totalCountNo > dataArray.length) {
            list = this.dataList.concat(data.records);
            this.dataList = list;
            this.totalCountNo = data.total;
            this.status = "loading";
          } else if (data.total <= 4) {
            this.dataList = data.records;
            this.status = "noMore";
          } else {
            this.dataList = data.records;
            this.status = "noMore";
          }

          // uni.navigateTo({
          //   url:
          //     "/pages/emergencyCourse/emergencyCourseDetail?id=" +
          //     res.data.id,
          // });
        }
      });
    },
  },
};
</script>

